<template>
<div class="container mt-5">
		  <div class="sample">
			<form v-if="!formDone">
        <AppProgress
          :current="fieldDone"
          :max="info.length"
        />
       
        
				<div>
					<AppField 
						v-for="item, i in info"
						:key="i"
						:label="item.label"
						:value="item.value"
						:valid="item.valid"
						@updated="onInput($event, i)"
					/>
					
				</div>
				<button class="btn btn-primary mt-5" :disabled="!formReady" @click="sendForm" type="button">
					Send Data
				</button>
			</form>
			<div v-else>
				<table class="table table-bordered">
					<tr v-for="field, idx in info" :key="idx">
						<td>{{ field.label}}</td>
						<td>{{ field.value}}</td>
					</tr>
				</table>
			</div>
		</div>
</div>

</template>

<script>
import AppField from './components/AppField';

import AppProgress from './components/Progress'



export default {
  components: {
    AppField,
    AppProgress  
  },
  data: () => ({
    info: [
      {
        label: 'Name',
        value: '',
        pattern: /^[a-zA-Z ]{2,30}$/
      },
      {
        label: 'Phone',
        value: '',
        pattern: /^[0-9]{7,14}$/
      },
      {
        label: 'Email',
        value: '',
        pattern: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      },
      {
        label: 'Some Field 1',
        value: '',
        pattern: /.+/
      },
      {
        label: 'Some Field 2',
        value: '',
        pattern: /.+/
      }
    ],
    formDone: false
  }),
  computed: {
    fieldDone() {

      return this.info.filter(item => item.valid).length

    },

    formReady() {
      return this.fieldDone >= this.info.length
    }
  },
  methods: {
    onInput(getValue, i){
      let field = this.info[i];
      field.value = getValue.trim();
      field.valid = field.pattern.test(getValue);
    },
    sendForm() {
      if (this.formReady) {
        this.formDone = true
      }
    }
  },
  created(){
      this.info.forEach(field => {
      field.valid = false;
    })
  }
}

</script>

