<template>
    <div class="progress">
        <div class="progress-bar" :style="progressStyles"></div>
    </div>
</template>

<script>
    export default {
        props: {
            max: {
                type: Number,
                default: 100,
            },
            current: {
                type: Number,
                required: true
            },
        },
        computed: {

            progressStyles() {
                return {width: this.value * 100 + '%'} 
      
            },
            value() {
                return this.current / this.max 
            }
        }
    }

    
</script>