<template>
<div class="form-group mt-3">
    <label>
        {{ label }}

        <fa-icon 
            v-if="activated"
            :class="valid ? 'text-success ml-10' : 'text-danger ml-10'"
            :icon="valid ? 'check-circle' : 'exclamation-circle'"
        />

      
    </label>
    <input type="text" class="form-control" :value="value" @input="onInput">
</div>
</template>

<script>
export default {
    props: {
        label: {
            type: String,
            required: true
        },
        value: {
            type: String,
            required: true
        },
        valid: {
            type: Boolean,
            required: true
        }
    },
    data() {
        return {
            activated: this.value != ''
        }
    },
    methods: {
        onInput(e){
            this.activated = true;
            const value = e.target.value;
            this.$emit('updated', value)
        },
    },
}
</script>
<style>
.myIcon{
    width: 25px;
    height: 25px;
    margin-left: 5px;
}
.ml-10 {
    margin-left: 10px;
}
</style>